


























































import Vue from 'vue'
import Component from 'vue-class-component'
import {timer} from 'rxjs';
import {shuffle} from 'lodash';
import {BIconArrowClockwise, BIconSkipStartFill} from 'bootstrap-vue'

Vue.component('BIconSkipStartFill', BIconSkipStartFill);
Vue.component('BIconArrowClockwise', BIconArrowClockwise);

@Component
export default class Shuffling extends Vue {
  lock = false;
  queue = []
  nodes = [];

  created() {
    this.init();
  }

  init() {
    this.queue = shuffle([
      {key: 0, value: 'Ali'},
      {key: 1, value: 'Ban'},
      {key: 2, value: 'Dan'},
      {key: 3, value: 'Eva'},
      {key: 4, value: 'Jay'},
      {key: 5, value: 'Kit'},
      {key: 6, value: 'Leo'},
      {key: 7, value: 'Max'},
      {key: 8, value: 'Rob'},
      {key: 9, value: 'Tom'}
    ]);
    this.nodes = [];
    this.lock = false;
  }

  linkedListIsEmpty() {
    return this.nodes.length === 0;
  }

  async add() {
    if (this.nodes.length > 0) {
      this.lock = true;
      const nodesContainer: HTMLElement = this.$el.querySelector('.nodes');
      nodesContainer.style.transition = 'padding 250ms';
      nodesContainer.style.paddingLeft = '210px';
      await timer(300).toPromise();
      nodesContainer.style.transition = null;
      nodesContainer.style.paddingLeft = null;
      this.lock = false;
    }

    this.nodes.unshift(this.queue.shift());
    if (!this.queue.length) this.lock = true;
  }
}
